import React from "react"
import { Link } from "gatsby"

const DOTS = "..."
const FIRST_PAGE = 1
const TO_FIRST = {
  page: FIRST_PAGE,
  name: FIRST_PAGE,
}

const Pagination = ({ location, pagination }) => {
  const { numPages, currentPage, fields } = pagination
  const arr = []
  const toLast = {
    page: numPages,
    name: numPages,
  }
  const end = currentPage + fields
  let start = FIRST_PAGE

  if (currentPage > fields) {
    start = currentPage - fields
  }

  if (currentPage > fields + 1) {
    const prevStart = start - 1
    arr.push(TO_FIRST)
    if (prevStart > 1) {
      arr.push({ page: start === 0 ? 1 : start - 1, name: DOTS })
    }
  }

  for (let i = start; i <= end; i++) {
    arr.push({ page: i, name: i })
    if (i === numPages) break
  }

  if (end < numPages) {
    const nextEnd = end + 1
    if (nextEnd < numPages) {
      arr.push({ page: nextEnd, name: DOTS })
    }
    arr.push(toLast)
  }

  console.log(arr)

  return (
    <ol className="pagination">
      {arr.map(f => {
        const { page, name } = f
        const key = `pagination-number${page}`
        if (page === currentPage) {
          return (
            <li key={key}>
              <span>{name}</span>
            </li>
          )
        } else {
          return (
            <li key={key}>
              <Link
                key={key}
                to={`${location.pathname.replace(/(\/[0-9]+$)|\/$/, "")}/${
                  page === FIRST_PAGE ? "" : page
                }`}
              >
                {name}
              </Link>
            </li>
          )
        }
      })}
    </ol>
  )
}

export default Pagination
