import React from "react"
import { Link } from "gatsby"

const BackButton = ({ location, path, title }) => {
  if (!path) {
    path = location.state?.prevPath
  }

  if (!title) {
    title = location.state?.prevTitle
  }

  return path && title ? (
    <Link id="go-back" to={path} itemProp="url">
      <span>&#x2190;</span> {title}
    </Link>
  ) : (
    <p></p>
  )
}

export default BackButton
